import { AnyObject, MixinClass } from '../../../utils';
import { IMaskProvider } from './IMaskProvider';
import { InputMaskLibProvider } from './providers';

export interface IInputMaskOptions {
  $inputElement?: HTMLInputElement | null;
  maskOptions?: AnyObject | string;
}

export interface IInputMaskState {
  value: string;
  rawValue: string;
}

export class InputMask implements MixinClass {
  MaskProvider = InputMaskLibProvider;
  maskProviderInstance?: IMaskProvider;

  // props
  $inputElement?: HTMLInputElement | null;
  maskOptions?: AnyObject | string;

  constructor(inputMaskOptions: Partial<IInputMaskOptions>, MaskProvider?) {
    if (inputMaskOptions.$inputElement) {
      this.$inputElement = inputMaskOptions.$inputElement;
    }

    if (inputMaskOptions.maskOptions) {
      this.maskOptions = inputMaskOptions.maskOptions;
    }

    if (MaskProvider) {
      this.MaskProvider = MaskProvider;
    }
  }

  onDidMount() {
    this.instantiateProvider();
  }

  onWillUnmount() {
    this.maskProviderInstance?.destroy();
  }

  onUpdate({ $inputElement, maskOptions }: IInputMaskOptions) {
    if (
      this.$inputElement !== $inputElement ||
      this.maskOptions !== maskOptions
    ) {
      this.$inputElement = $inputElement;
      this.maskOptions = maskOptions;

      if (this.maskProviderInstance && this.$inputElement && this.maskOptions) {
        this.maskProviderInstance.update(this.$inputElement, this.maskOptions);
      } else {
        this.maskProviderInstance?.destroy();
        this.instantiateProvider();
      }
    }
  }

  instantiateProvider() {
    if (this.$inputElement && this.maskOptions) {
      this.maskProviderInstance = new this.MaskProvider(
        this.$inputElement,
        this.maskOptions
      );
    }
  }

  getUnmaskedValue() {
    return this.maskProviderInstance?.getRawValue();
  }

  setValue(value: string) {
    this.maskProviderInstance?.setValue(value);
  }
}
