import Inputmask from 'inputmask';
import isEqual from 'lodash/isEqual';
import {
  IMaskProvider,
  MaskProviderInstance,
  MaskProviderOptions
} from '../IMaskProvider';
import { AnyObject } from '../../../../utils';

export class InputMaskLibProvider implements IMaskProvider {
  instance?: Inputmask.Instance;
  $element?: HTMLElement;
  maskOptions?: MaskProviderOptions;

  constructor($element: HTMLElement, maskOptions: AnyObject | string) {
    this.init($element, maskOptions);
  }

  destroy(maskProviderInstance?: MaskProviderInstance): void {
    this.instance?.remove();
  }

  setValue(value: string): void {
    this.instance?.setValue(value);
  }

  getMaskFromString(value: string): MaskProviderOptions {
    return stringToMaskOptionsMap[value] || value;
  }

  getFormattedOptions(options: MaskProviderOptions) {
    return typeof options === 'string'
      ? this.getMaskFromString(options)
      : options;
  }

  getFormattedValue(): string {
    return (this.instance as any)?.el?.value || '';
  }

  getRawValue(): string {
    return this.instance?.unmaskedvalue() || '';
  }

  init($element: HTMLElement, maskOptions: MaskProviderOptions) {
    this.$element = $element;
    this.maskOptions = maskOptions;
    const params = this.getFormattedOptions(maskOptions);
    const maskInstance =
      // Getting around typescript
      typeof params === 'string' ? Inputmask(params) : Inputmask(params);
    this.instance = maskInstance.mask($element);
  }

  update($element: HTMLElement, maskOptions: MaskProviderOptions) {
    if (this.$element !== $element || !isEqual(this.maskOptions, maskOptions)) {
      this.instance && this.destroy(this.instance);
      this.init($element, maskOptions);
    }
  }
}

const dateOptions = {
  alias: 'datetime',
  inputFormat: 'dd.mm.yyyy',
  placeholder: '__.__.____',
  onBeforeMask: (v) => {
    v = new Date(v);
    const withPad = (num) => String(num).padStart(2, '0');
    const day = withPad(v.getDate());
    const month = withPad(v.getMonth() + 1);
    const year = v.getFullYear();
    return `${day}.${month}.${year}`;
  }
};

const timeOptions = {
  mask: '99:99',
  inputFormat: 'HH:mm',
  placeholder: '00:00',
  onBeforeMask: (v) => {
    if (v.length <= 4) {
      return `0${v}`;
    }
    return v;
  }
};

const stringToMaskOptionsMap: Record<string, MaskProviderOptions> = {
  phone: '+7 999 999-99-99',
  time: timeOptions,
  date: dateOptions
};

Inputmask.extendAliases({
  date: dateOptions,
  time: timeOptions
});
